import usePositionStore from '@/store/position'
import { PosBaseInfo } from '@/types'
import useClmmSDKStore from '@cetus/stores/src/useClmmSDKStore'
import envConfigs from '@cetus/types/src/config/envConfigs'
import { d } from '@cetus/utils'
import { DataPage } from '@cetusprotocol/cetus-sui-clmm-sdk'
import useGetFarmsPosRewards from './useGetFarmsPosRewards'
import useGetPosApiPoolData from './useGetPosApiPoolData'
import useGetPosfees from './useGetPosfees'
import useGetPosLiquiditys from './useGetPosLiquiditys'
import useGetPosPools from './useGetPosPools'
import useGetPosRewards from './useGetPosRewards'
import usePosHelper from './usePosHelper'
import useWrapPosData from './useWrapPosData'

export default function usePositionList() {
  const { clmmSdk } = useClmmSDKStore()
  const { buildPositionType, buildFarmsPositionType, buildBurnPositionType } = usePosHelper()
  const { buildPosBaseInfo } = useWrapPosData()
  const { setPosBaseList, setFullRangePosBaseList, setPosBaseListLoading } = usePositionStore()

  const { getPosPoolsOriginalObj, getPosPoolsRelatedData } = useGetPosPools()
  const { getPosLiquidityData } = useGetPosLiquiditys()
  const { getPosFeeData } = useGetPosfees()
  const { getPosApiPoolData } = useGetPosApiPoolData()
  const { getPosRewardsData } = useGetPosRewards()
  const { getFarmsRewardsData } = useGetFarmsPosRewards()

  const getPositionBaseList = async (account: string, clmmPool?: string): Promise<PosBaseInfo[]> => {
    console.log('üöÄ ~ getPositionBaseList ~ clmmPool:', clmmPool)
    setPosBaseListLoading(true)
    try {
      const ownerRes = await clmmSdk.fullClient.getOwnedObjectsByPage(account, {
        options: { showType: true, showContent: true, showOwner: true },
        filter: {
          MatchAny: [
            {
              Package: envConfigs.clmm_sdk_options.clmm_pool.package_id
            },
            {
              StructType: buildFarmsPositionType
            },
            {
              StructType: buildBurnPositionType
            }
          ]
        }
      })

      console.log('üöÄ ~ file: usePositionList.ts:25 ~ getPositionList ~ ownerRes:', ownerRes)

      let result: PosBaseInfo[] = await formatPosBaseList(ownerRes, clmmPool)

      setPosBaseList(result)
      setPosBaseListLoading(false)
      if (result?.length == 0) {
        setFullRangePosBaseList([])
      }

      return result
    } catch (error) {
      console.error('üöÄ ~ getPositionBaseList ~ 79 ~ error:', error)
      if (error instanceof TypeError) {
        setPosBaseListLoading(false)
        setPosBaseList([])
        //return 'Something went wrong, please change the rpc node and try again'
        return []
      }
      return []
    }
  }

  const formatPosBaseList = async (ownerRes: DataPage<any>, clmmPool?: string) => {
    const posBaseList: any = []

    for (const item of ownerRes.data as any[]) {
      const info = await buildPosBaseInfo(item)
      // console.log('üöÄ ~ file: usePositionList.ts:37 ~ getPositionList ~ info:', info)
      if (d(info?.liquidity).lte(0)) continue
      if (!info?.displayTokenA || !info?.displayTokenB) continue
      if (info) {
        posBaseList.push(info)
      }
    }

    console.log('üöÄ ~ file: usePositionList.ts:33 ~ getPositionList ~ posBaseList:', posBaseList)

    const sortBaseList = posBaseList.sort((a: any, b: any) => b.version - a.version)

    let result: PosBaseInfo[] = []
    if (!clmmPool) {
      result = sortBaseList
    } else {
      result = sortBaseList?.filter((item: PosBaseInfo) => item.clmmPool === clmmPool)
    }

    return result
  }

  const getPosRelatedData = async (posBaseList: PosBaseInfo[], isFarms: boolean = false) => {
    console.log('üöÄ ~ getPosRelatedData ~ posBaseList:', isFarms, posBaseList)
    getFarmsRewardsData(posBaseList)
    if (!isFarms) {
      console.log('üöÄ ~ getPosRelatedData ~ isFarms:', posBaseList)
      getPosApiPoolData(posBaseList)
      getPosFeeData(posBaseList)
    }
    try {
      const posPoolsOriginalData = await getPosPoolsOriginalObj(posBaseList)
      console.log('üöÄ ~ getPosRelatedData ~ posPoolsOriginalData:', posPoolsOriginalData)
      getPosPoolsRelatedData(posBaseList, posPoolsOriginalData) //hookÈáåÈù¢ÂÖ≥‰∫éposPoolsOriginalDataÊï∞ÊçÆÂ∑≤ÁªèÂ§ÑÁêÜ
      if (Object.keys(posPoolsOriginalData)?.length > 0) {
        getPosLiquidityData(posBaseList, posPoolsOriginalData)
        if (!isFarms) {
          getPosRewardsData(posBaseList, posPoolsOriginalData)
        }
      }
    } catch (error) {
      console.error('üöÄ ~ getPosRelatedData ~ error: 96', error)
    }
  }

  return {
    formatPosBaseList,
    getPositionBaseList,
    getPosRelatedData
  }
}
